.content-wrapper {
  padding: 5%;
  font-size: 1.5em;
  font-family: 'Oswald', sans-serif;
  border-color: white !important;
}

.cards-grid {
  margin-top: 55em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 3%;
  padding: 3%;
  max-width: 100%;
  max-height: 100%;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 959px) {
  .cards-grid {
    grid-template-columns: 1fr;
    margin-top: 155em;
    padding: 5%;
    grid-gap: 5%;
  }
}

.box-buttons {
  padding: 5%;
  text-align: center;
}

.card-titles {
  padding: 10%;
}

.card-container {
  width: 95%;
  background-color: rgba(247, 187, 198, 0.79);
  padding: 2%;
  border-radius: 5%;
}

.project-img {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}
.callejeros {
  width: 35%;
  height: auto;
  align-items: center;
}
.middle {
  transition: 0.5s ease;
  opacity: 0;
  position: relative;
  top: 0%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  font-size: 1.8em;
  font-family: 'Roboto', sans-serif;
  align-content: center;
}

.card-hoverable:hover .project-img {
  opacity: 0.1;
}

.card-hoverable:hover .middle {
  opacity: 1;
}

.text-btn {
  background-color: turquoise;
  color: white;
  font-size: 16px;
  padding: 16px 32px;
  margin-left: 1em;
}

.footer-p {
  font-family: 'Oswald', sans-serif;
  font-size: 200%;
  color: black;
}
.footer-sticky-area {
  margin-top: 140em;
  margin-left: 15%;
}
@media screen and (max-width: 959px) {
  .footer-sticky-area {
    margin-top: 275em;
    margin-left: 5%;
  }
  .footer-p {
    font-size: 150%;
  }
}

.ancor-footer:hover {
  color: palevioletred;
}
.footer-style {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 20%;
  height: auto;
  background-color: rgba(255, 255, 255, 0.626);
  text-align: center;
  padding: 1%;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}
.footer-txt {
  font-size: 2em;
}
.footer-btn {
  background-color: transparent;
  border: none;
}
.footer-btn:hover {
  border: 3px dashed turquoise;
}

@media screen and (max-width: 959px) {
  .footer-txt {
    font-size: 1.2em;
  }
  .footer-style {
    border-bottom-left-radius: 0%;
    border-top-right-radius: 0%;
  }
}

